import axios from '@/api/axios';
import { getItem } from "@/helpers/serializeLocalStorage"

const register = credential => axios.post('/users', { user: credential });

const login = credential => axios.post('/users/login', { user: credential });

const currentUser = () => axios.get('/user');

const updateUser = settingInput => axios.put('/user', { user: settingInput }).then(response => response.data.user);

axios.interceptors.request.use((config) => {
    const token = getItem('successToken');
    config.headers.Authorization = token ? `Token ${token}` : '';
    return config;
});

export default {
    register,
    login,
    currentUser,
    updateUser
}