<template>
  <button class="user-profile__btn" @click="changeFollow">
    {{ contentBtn }}
  </button>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "ButtonFollow",
  props: {
    isFollowing: {
      type: Boolean,
      required: true,
    },
    slug: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      isFollowingOptimistic: this.isFollowing,
    };
  },
  computed: {
    contentBtn() {
      return this.isFollowingOptimistic
        ? `Unfollow ${this.slug}`
        : `Follow ${this.slug}`;
    },
  },
  methods: {
    ...mapActions("changeFollow", ["updateFollow"]),
    changeFollow() {
      this.isFollowingOptimistic = !this.isFollowingOptimistic;
      this.updateFollow({
        isFollowing: this.isFollowingOptimistic,
        slug: this.slug,
      });
    },
  },
};
</script>

