<template>
  <div>
    <Loading v-if="false" />
    <div class="user-profile" v-if="userProfileData">
      <div class="user-profile__banner">
        <div class="container user-profile__container">
          <img
            :src="userProfileData.image"
            alt="userProfile-img"
            class="user-profile__img"
          />
          <p class="user-profile__username">{{ userProfileData.username }}</p>
          <router-link
            class="user-profile__btn"
            :to="{ name: 'settings' }"
            v-if="isCurrentUserProfile"
            >Edit Prifile Settings</router-link
          >
          <ButtonFollow
            :is-following="userProfileData.following"
            :slug="userProfileSlug"
            v-if="!isCurrentUserProfile"
          />
          {{ routeName }}
        </div>
      </div>
    </div>
    <div class="container" v-if="userProfileData">
      <ul class="toggle-feed__list">
        <li class="toggle-feed__item">
          <router-link
            class="toggle-feed__link"
            :to="{ name: 'userProfile' }"
            :class="{ 'active-feed': routeName === 'userProfile' }"
            >My Articles</router-link
          >
        </li>

        <li class="toggle-feed__item">
          <router-link
            class="toggle-feed__link"
            :to="{ name: 'userProfileFavorites' }"
            :class="{ 'active-feed': routeName === 'userProfileFavorites' }"
            >Favorites Articles</router-link
          >
        </li>
      </ul>
      <Feed :api-url="apiUrl" />
    </div>
  </div>
</template>

<script>
import Loading from "./Loading.vue";
import Feed from "./Feed.vue";
import ButtonFollow from "./ButtonFollow.vue";

import { mapActions, mapGetters } from "vuex";

export default {
  name: "UserProfile",
  components: {
    Loading,
    Feed,
    ButtonFollow,
  },
  mounted() {
    this.getProfile();
  },
  methods: {
    ...mapActions("userProfile", ["getUserProfile"]),
    getProfile() {
      this.getUserProfile({ slug: this.userProfileSlug });
    },
  },
  computed: {
    ...mapGetters("userProfile", ["userProfileData", "isLoading"]),
    ...mapGetters("auth", ["authUser"]),
    userProfileSlug() {
      return this.$route.params.slug;
    },
    routeName() {
      return this.$route.name;
    },
    isCurrentUserProfile() {
      if (!this.authUser || !this.userProfileData) {
        return false;
      }
      return this.authUser.username === this.userProfileData.username;
    },
    apiUrl() {
      const isFavorites = this.$route.path.includes("favorites");

      return isFavorites
        ? `/articles?favorited=${this.userProfileSlug}`
        : `/articles?author=${this.userProfileSlug}`;
    },
  },
  watch: {
    userProfileSlug() {
      this.getProfile();
    },
  },
};
</script>

