import articleApi from '@/api/article';
import mutations from '../mutations';

const {
    GET_ARTICLE_START,
    GET_ARTICLE_SUCCESS,
    GET_ARTICLE_FAILED,

    DELETE_ARTICLE_START,
    DELETE_ARTICLE_SUCCESS,
    DELETE_ARTICLE_FAILED,

    CREATE_ARTICLE_START,
    CREATE_ARTICLE_SUCCESS,
    CREATE_ARTICLE_FAILED,

    UPDATE_ARTICLE_START,
    UPDATE_ARTICLE_SUCCESS,
    UPDATE_ARTICLE_FAILED,
} = mutations;

export default {
    namespaced: true,
    state: {
        article: null,
        error: null,
        createError: null,
        isLoading: false,
        isSubmitting: false,
    },
    getters: {
        articleInfo: (state) => state.article,
        error: (state) => state.error,
        createError: (state) => state.createError,
        isLoading: (state) => state.isLoading,
        isSubmitting: (state) => state.isSubmitting,
    },
    mutations: {
        [GET_ARTICLE_START](state) {
            state.isLoading = true;
            state.error = null;
        },
        [GET_ARTICLE_SUCCESS](state, data) {
            state.isLoading = false;
            state.error = null;
            state.article = data;
        },
        [GET_ARTICLE_FAILED](state, errors) {
            state.isLoading = false;
            state.error = errors;
            state.article = null;
        },
        [DELETE_ARTICLE_START](state) {
            state.isLoading = true;
            state.error = null;
        },
        [DELETE_ARTICLE_SUCCESS](state) {
            state.isLoading = false;
            state.error = null;
        },
        [DELETE_ARTICLE_FAILED](state, errors) {
            state.isLoading = false;
            state.error = errors;
            state.article = null;
        },
        [CREATE_ARTICLE_START](state) {
            state.isLoading = true;
            state.error = null;
            state.isSubmitting = true;
        },
        [CREATE_ARTICLE_SUCCESS](state, article) {
            state.isLoading = false;
            state.error = null;
            state.article = article;
            state.isSubmitting = false;
        },
        [CREATE_ARTICLE_FAILED](state, errors) {
            state.isLoading = false;
            state.createError = errors;
            state.article = null;
            state.isSubmitting = false;
        },
        [UPDATE_ARTICLE_START](state) {
            state.isLoading = true;
            state.error = null;
            state.isSubmitting = true;
        },
        [UPDATE_ARTICLE_SUCCESS](state, article) {
            state.isLoading = false;
            state.error = null;
            state.article = article;
            state.isSubmitting = false;
        },
        [UPDATE_ARTICLE_FAILED](state, errors) {
            state.isLoading = false;
            state.createError = errors;
            state.article = null;
            state.isSubmitting = false;
        },
    },
    actions: {
        getArticle({ commit }, slug) {
            return new Promise(resolve => {
                commit(GET_ARTICLE_START);
                articleApi.getArticle(slug).then(result => {
                    commit(GET_ARTICLE_SUCCESS, result);
                    console.log(result);
                    resolve(result);
                }).catch((errors) => {
                    commit(GET_ARTICLE_FAILED, errors);
                });
            })
        },
        deleteArticle({ commit }, slug) {
            return new Promise(resolve => {
                commit(DELETE_ARTICLE_START);
                articleApi.deleteArticle(slug).then(() => {
                    commit(DELETE_ARTICLE_SUCCESS);
                    resolve();
                }).catch((errors) => {
                    commit(DELETE_ARTICLE_FAILED, errors);
                });
            })
        },
        createArticle({ commit }, articleInput) {
            return new Promise(resolve => {
                commit(CREATE_ARTICLE_START);
                articleApi.createArticle(articleInput).then((response) => {
                    commit(CREATE_ARTICLE_SUCCESS, response);
                    resolve(response);
                }).catch((errors) => {
                    commit(CREATE_ARTICLE_FAILED, errors.response.data.errors);
                });
            })
        },
        updateArticle({ commit }, { articleInput, slug }) {
            return new Promise(resolve => {
                commit(UPDATE_ARTICLE_START);
                articleApi.updateArticle({ articleInput, slug }).then((response) => {
                    commit(UPDATE_ARTICLE_SUCCESS, response);
                    resolve();
                }).catch((errors) => {
                    commit(UPDATE_ARTICLE_FAILED, errors.response.data.errors);
                });
            })
        },
        // createArticle
    },
}