<template>
  <ul class="pagination">
    <li
      class="pagination__item"
      v-for="page in getRange"
      :class="{ 'pagination-active': currentPage === page }"
      :key="page"
      @click="clickM(page)"
    >
      <router-link
        class="pagination__link"
        :to="{ path: url, query: { page: page } }"
      >
        {{ page }}
      </router-link>
    </li>
  </ul>
</template>

<script>
import { range } from "@/helpers/utils";
export default {
  name: "Pagination",
  props: {
    total: {
      type: Number,
      required: true,
    },
    limit: {
      type: Number,
      required: true,
    },
    currentPage: {
      type: Number,
      required: true,
    },
    url: {
      type: String,
      required: true,
    },
  },
  computed: {
    getRange() {
      const lengthRange = Math.ceil(this.total / this.limit);
      return range(1, lengthRange);
    },
  },
  methods: {
    clickM(page) {
      console.log(this.$route.query.page, page);
    },
  },
};
</script>

