<template>
  <div class="feeds__wrapper">
    <Loading v-if="isLoading" />
    <Errors v-if="errors" />
    <div v-if="feeds" class="feeds__article">
      <div
        class="article-preview"
        v-for="(article, index) in feeds.articles"
        :key="index"
      >
        <div class="article-meta">
          <div class="article__user-info">
            <router-link
              class="article__user-img"
              :to="{
                name: 'userProfile',
                params: { slug: article.author.username },
              }"
            >
              <img
                class="topbar__user-img"
                :src="article.author.image"
                alt="user-image"
              />
            </router-link>
            <div class="info">
              <router-link
                class="article__author"
                :to="{
                  name: 'userProfile',
                  params: { slug: article.author.username },
                }"
              >
                {{ article.author.username }}
              </router-link>
              <span class="article__date">{{
                new Date(article.createdAt)
              }}</span>
            </div>
          </div>
          <AddToFavorite
            v-if="isLogin"
            :is-favorite="article.favorited"
            :favorites-count="article.favoritesCount"
            :article-slug="article.slug"
          />
        </div>
        <router-link
          class="article__preview-link"
          :to="{
            name: 'article',
            params: { slug: article.slug },
          }"
        >
          <h2 class="article__title">{{ article.title }}</h2>
          <p class="article__description">{{ article.description }}</p>
          <span class="article__read-more">Read more...</span>
        </router-link>
        <TagList v-if="article.tagList" :tag-list="article.tagList" />
      </div>
      <Paggination
        v-if="feeds.articlesCount > limit"
        :total="feeds.articlesCount"
        :limit="limit"
        :url="baseUrl"
        :current-page="currentPage"
      />
    </div>
  </div>
</template>

<script>
import Paggination from "@/components/Pagination";
import Loading from "@/components/Loading";
import Errors from "@/components/Errors";
import TagList from "@/components/TagList";
import AddToFavorite from "@/components/AddToFavorite";
import { limit } from "@/helpers/vars";
import { mapActions, mapGetters } from "vuex";
import { stringify, parseUrl } from "query-string";

export default {
  name: "Feed",
  components: {
    Paggination,
    Loading,
    Errors,
    TagList,
    AddToFavorite,
  },
  props: {
    apiUrl: {
      type: String,
      required: true,
    },
  },
  mounted() {
    this.getFeeds();
    // console.log(this.$route.path === "/");
  },
  data() {
    return {
      limit: limit,
    };
  },
  methods: {
    ...mapActions("feedStore", ["fetchFeeds"]),
    getFeeds() {
      const parsedUrl = parseUrl(this.apiUrl);
      const strigifiedParams = stringify({
        limit,
        offset: this.offset,
        ...parsedUrl.query,
        // tag: slug,
      });
      const apiUrlWithParams = `${parsedUrl.url}?${strigifiedParams}`;
      this.fetchFeeds({
        apiUrl: apiUrlWithParams,
      });
      // console.log(apiUrlWithParams);
    },
  },
  computed: {
    ...mapGetters("feedStore", ["feeds", "isLoading", "errors"]),
    ...mapGetters("auth", ["isLogin"]),
    baseUrl() {
      return this.$route.path;
    },
    currentPage() {
      return Number(this.$route.query.page || "1");
    },
    pageSlug() {
      return this.$route.params.slug;
    },
    offset() {
      return this.currentPage * limit - limit;
    },
  },
  watch: {
    currentPage() {
      this.getFeeds();
    },
    pageSlug() {
      console.log(this.$route.params.slug);
      this.getFeeds();
    },
    apiUrl() {
      this.getFeeds();
    },
  },
};
</script>

