import mutations from "../mutations"
import auth from "@/api/auth"
import { setItem } from "@/helpers/serializeLocalStorage"


const { REGISTER_START } = mutations;
const { REGISTER_SUCCESS } = mutations;
const { REGISTER_FAILED } = mutations;
const { LOGIN_START } = mutations;
const { LOGIN_SUCCESS } = mutations;
const { LOGIN_FAILED } = mutations;
const { CURRENT_USER_START } = mutations;
const { CURRENT_USER_SUCCESS } = mutations;
const { CURRENT_USER_FAILED } = mutations;
const { UPDATE_USER_START } = mutations;
const { UPDATE_USER_SUCCESS } = mutations;
const { UPDATE_USER_FAILED } = mutations;
const { LOGOUT } = mutations;

export default {
    namespaced: true,
    state: {
        isSubmiting: false,
        isLoading: false,
        isLogin: null,
        authUser: null,
        authErrors: null,
    },
    getters: {
        isSubmiting: (state) => state.isSubmiting,
        authErrors: (state) => state.authErrors,
        authUser: (state) => state.authUser,
        isLogin: (state) => Boolean(state.isLogin),
        isAnonymous: (state) => state.isLogin === false,
    },
    mutations: {
        [REGISTER_START](state) {
            state.isSubmiting = true;
            state.authErrors = null;
        },
        [REGISTER_SUCCESS](state, user) {
            state.isSubmiting = false;
            state.authUser = user;
            state.isLogin = true;

        },
        [REGISTER_FAILED](state, errors) {
            state.isSubmiting = false;
            state.authErrors = errors;
        },
        [LOGIN_START](state) {
            state.isSubmiting = true;
            state.authErrors = null;
        },
        [LOGIN_SUCCESS](state, user) {
            state.isSubmiting = false;
            state.authUser = user;
            state.isLogin = true;

        },
        [LOGIN_FAILED](state, errors) {
            state.isSubmiting = false;
            state.authErrors = errors;
        },
        [LOGOUT](state) {
            state.isLoading = false;
            state.authUser = null;
            state.isLogin = false;
        },
        [CURRENT_USER_START](state) {
            state.isLoading = true;
            state.authErrors = null;
        },
        [CURRENT_USER_SUCCESS](state, user) {
            state.isLoading = false;
            state.authUser = user;
            state.isLogin = true;

        },
        [CURRENT_USER_FAILED](state) {
            state.isLoading = false;
            state.authUser = null;
            state.isLogin = false;
        },
        [UPDATE_USER_START](state) {
            state.isLoading = true;
            state.authErrors = null;
        },
        [UPDATE_USER_SUCCESS](state, user) {
            state.isLoading = false;
            state.authUser = user;

        },
        [UPDATE_USER_FAILED](state) {
            state.isLoading = false;
        },
    },
    actions: {
        async fetchAuth({ commit }, credential) {
            return new Promise(resolve => {
                commit(REGISTER_START);
                auth.register(credential)
                    .then(response => {
                        setItem('successToken', response.data.user.token);
                        commit(REGISTER_SUCCESS, response.data.user);
                        resolve(response.data.user);
                    })
                    .catch((error) => commit(REGISTER_FAILED, error.response.data.errors));
            });

        },
        async fetchLogin({ commit }, credential) {
            // try {
            //     commit(LOGIN_START);
            //     const response = await auth.login(credential);
            //     setItem('successToken', response.data.user.token);
            //     commit(LOGIN_SUCCESS, response.data.user);
            //     return true;
            // } catch (error) {
            //     commit(LOGIN_FAILED, error.response.data.errors);
            //     return false;
            // }
            return new Promise(resolve => {
                commit(LOGIN_START);
                auth.login(credential)
                    .then(response => {
                        setItem('successToken', response.data.user.token);
                        commit(LOGIN_SUCCESS, response.data.user);
                        resolve(response.data.user);
                    })
                    .catch((error) => commit(LOGIN_FAILED, error.response.data.errors));
            });

        },
        async fetchCurrentUser({ commit }) {
            return new Promise(resolve => {

                commit(CURRENT_USER_START);
                auth.currentUser()
                    .then(response => {
                        commit(CURRENT_USER_SUCCESS, response.data.user);
                        resolve(response.data.user);
                    })
                    .catch(() => commit(CURRENT_USER_FAILED));
            });

        },
        async updateCurrentUser({ commit }, { settingInput }) {
            return new Promise(resolve => {
                commit(UPDATE_USER_START);
                auth.updateUser(settingInput)
                    .then((response) => {
                        commit(UPDATE_USER_SUCCESS, response);
                        resolve(response);
                    }).catch(() => commit(UPDATE_USER_FAILED));
            })
        },
        logout({ commit }) {
            return new Promise(resolve => {
                setItem('successToken', '');
                commit(LOGOUT);
                resolve();
            })
        }

    },
}