<template>
  <div class="container">
    <form class="article__form" @submit.prevent="onSubmit">
      <ValidationErrors v-if="errors" :validation-errors="errors" />
      <label>
        <input
          class="article__input"
          type="text"
          placeholder="Article Title"
          v-model="title"
        />
      </label>
      <label>
        <input
          class="article__input"
          type="text"
          placeholder="What's this article about?"
          v-model="description"
        />
      </label>
      <label>
        <textarea
          class="article__textarea"
          placeholder="Write your article (in markdown)"
          v-model="body"
        ></textarea>
      </label>
      <label>
        <input
          class="article__input"
          type="text"
          placeholder="Enter tags"
          v-model="tags"
        />
      </label>
      <button class="article__form-btn">Submit</button>
    </form>
  </div>
</template>

<script>
import ValidationErrors from "./ValidationErrors.vue";

export default {
  name: "ArticleForm",
  components: {
    ValidationErrors,
  },
  props: {
    initialValues: {
      type: Object,
      required: true,
    },
    errors: {
      type: Object,
    },
    isSubmitting: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      title: this.initialValues.title,
      description: this.initialValues.description,
      body: this.initialValues.body,
      tags: this.tags ? this.initialValues.tags.join(" ") : "",
    };
  },
  methods: {
    onSubmit() {
      const form = {
        title: this.title,
        description: this.description,
        body: this.body,
        tagList: this.tags ? this.tags.split(" ") : [],
      };
      this.$emit("articleSubmit", form);
    },
  },
};
</script>

