import changeFollow from '@/api/changeFollow';
import mutations from '../mutations';

const {
    UPDATE_FOLLOW_START,
    UPDATE_FOLLOW_SUCCESS,
    UPDATE_FOLLOW_FAILED,
} = mutations;

export default {
    namespaced: true,
    state: {},
    getters: {},
    mutations: {
        [UPDATE_FOLLOW_START]() { },
        [UPDATE_FOLLOW_SUCCESS]() { },
        [UPDATE_FOLLOW_FAILED]() { },
    },
    actions: {
        async updateFollow({ commit }, { slug, isFollowing }) {
            return new Promise(resolve => {
                commit(UPDATE_FOLLOW_START);
                commit(UPDATE_FOLLOW_START);
                commit(UPDATE_FOLLOW_START);
                const promise = isFollowing
                    ? changeFollow.addToFollow(slug)
                    : changeFollow.deleteToFollow(slug);
                promise
                    .then((response) => {
                        commit(UPDATE_FOLLOW_SUCCESS);
                        resolve(response);
                    })
                    .catch((result) => {
                        commit(UPDATE_FOLLOW_FAILED, result.data.errors);
                    })
            })
        }
    },
}