<template>
  <div class="valid-errors__wrapper">
    <ul class="valid-errors__list">
      <li class="valid-error__item" v-for="(error, index) in listErrors" :key="index">{{ error }}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "ValidationErrors",
  props: {
    validationErrors: {
      type: Object,
      required: true,
    },
  },
  computed: {
    listErrors() {
      return Object.keys(this.validationErrors).map((key) => {
        const messages = this.validationErrors[key].join(",");

        return `${key} ${messages}`.toString();
      });
    },
  },
};
</script>

