<template>
  <button
    class="article__favorites-add"
    :class="{ active: isFavoriteOptimistic }"
    @click="onClick"
  >
    <span class="icon-heart favorites-add-btn"></span>
    <span class="favorites-add-count"
      >&nbsp;{{ favoritesCountOptimistic }}</span
    >
  </button>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "AddToFavorite",
  props: {
    isFavorite: {
      type: Boolean,
      required: true,
    },
    favoritesCount: {
      type: Number,
      required: true,
    },
    articleSlug: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      isFavoriteOptimistic: this.isFavorite,
      favoritesCountOptimistic: this.favoritesCount,
      articleSlugOptimistic: this.articleSlug,
    };
  },
  methods: {
    ...mapActions("addToFavorite", ["updateFavoriteArticle"]),
    onClick() {
      if (this.isFavoriteOptimistic) {
        this.favoritesCountOptimistic -= 1;
      } else {
        this.favoritesCountOptimistic += 1;
      }
      this.isFavoriteOptimistic = !this.isFavoriteOptimistic;
      this.updateFavoriteArticle({
        slug: this.articleSlug,
        isFavorite: this.isFavoriteOptimistic,
      });
    },
  },
};
</script>

