<template>
  <div>
    <Loading v-if="isLoading" />
    <ArticleForm
      @articleSubmit="onSubmit"
      :initial-values="initialValues"
      :errors="createError"
      :is-submitting="isSubmitting"
    />
  </div>
</template>

<script>
import ArticleForm from "./ArticleForm.vue";
import Loading from "./Loading.vue";

import { mapActions, mapGetters } from "vuex";

export default {
  name: "CreateArticle",
  components: {
    ArticleForm,
    Loading,
  },
  data() {
    return {
      initialValues: {
        title: "",
        description: "",
        body: "",
        tags: [],
      },
    };
  },
  methods: {
    onSubmit(form) {
      console.log(form);
      this.createArticle(form).then((article) => {
        this.$router.push({
          name: "article",
          params: {
            slug: article.slug,
          },
        });
      });
    },
    ...mapActions("article", ["createArticle"]),
  },
  computed: {
    ...mapGetters("article", [
      "isSubmitting",
      "createError",
      "isLoading",
      "articleInfo",
    ]),
  },
};
</script>

