import addToFavorite from '@/api/addToFavorite'
import mutations from '../mutations'

const {
    UPDATE_FAVORITE_START,
    UPDATE_FAVORITE_SUCCESS,
    UPDATE_FAVORITE_FAILED,
} = mutations

export default {
    namespaced: true,
    state: {},
    getters: {},
    mutations: {
        [UPDATE_FAVORITE_START]() { },
        [UPDATE_FAVORITE_SUCCESS]() { },
        [UPDATE_FAVORITE_FAILED]() { },
    },
    actions: {
        updateFavoriteArticle({ commit }, { slug, isFavorite }) {
            return new Promise(resolve => {
                commit(UPDATE_FAVORITE_START);
                const promise
                    = isFavorite
                        ? addToFavorite.addToFavotite(slug)
                        : addToFavorite.deleteFromFavotite(slug);
                console.log(promise);
                promise
                    .then((article) => {
                        commit(UPDATE_FAVORITE_SUCCESS, article);
                        resolve(article)
                    })
                    .catch(() => {
                        commit(UPDATE_FAVORITE_FAILED);
                    });

            })
        }
    },
}