<template>
  <div class="register">
    <div class="register__wrapper">
      <h2 class="title register__title">Sign up</h2>
      <p class="register__link">
        <router-link :to="{ name: 'login' }">Have an account?</router-link>
      </p>
      <ValidationErrors v-if="authErrors" :validation-errors="authErrors" />
      <form class="register__form" @submit.prevent="onSubmit" action="">
        <label class="register__label">
          <input
            class="register__input"
            type="text"
            placeholder="Name"
            v-model="username"
          />
        </label>
        <label class="register__label">
          <input
            class="register__input"
            type="email"
            placeholder="E-mail"
            v-model="email"
          />
        </label>
        <label class="register__label">
          <input
            class="register__input"
            type="password"
            placeholder="Password"
            v-model="password"
          />
        </label>

        <button class="register__btn" type="submit" :disabled="isSubmiting">
          Submit
        </button>
      </form>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import { mapGetters } from "vuex";

import ValidationErrors from "../components/ValidationErrors";

export default {
  name: "Register",
  components: { ValidationErrors },
  data() {
    return {
      username: "",
      email: "",
      password: "",
    };
  },
  computed: {
    ...mapGetters("auth", ["isSubmiting", "authErrors", "isLogin"]),
  },
  methods: {
    onSubmit() {
      console.log("test");
      this.fetchAuth({
        email: this.email,
        username: this.username,
        password: this.password,
      }).then(() => {
        this.$router.push({ name: "globalFeed" });
      });
    },
    ...mapActions("auth", ["fetchAuth"]),
  },
};
</script>

