
import mutations from "../mutations"
import popularTags from "../../api/popularTags";

const {
    GET_POPULAR_TAGS_START,
    GET_POPULAR_TAGS_SUCCESS,
    GET_POPULAR_TAGS_FAILED
} = mutations;

export default {
    namespaced: true,
    state: {
        popularTags: null,
        isLoading: false,
        errors: null
    },
    getters: {
        popularTags: ({ popularTags }) => popularTags,
        isLoading: ({ isLoading }) => isLoading,
        errors: ({ errors }) => errors,
    },
    mutations: {
        [GET_POPULAR_TAGS_START](state) {
            state.isLoading = true;
            state.errors = null;
            state.popularTags = null;
        },
        [GET_POPULAR_TAGS_SUCCESS](state, data) {
            state.isLoading = false;
            state.popularTags = data;
        },
        [GET_POPULAR_TAGS_FAILED](state, errors) {
            state.isLoading = false;
            state.errors = errors;
            state.popularTags = null;
        },
    },
    actions: {
        fetchPopularTags({ commit }) {
            return new Promise(resolve => {
                commit(GET_POPULAR_TAGS_START);
                popularTags.getPopularTags()
                    .then(response => {
                        commit(GET_POPULAR_TAGS_SUCCESS, response);
                        return resolve(response);
                    })
                    .catch(errors => {
                        commit(GET_POPULAR_TAGS_FAILED, errors);
                    })
            });
        }
    },
}