<template>
  <nav class="topbar">
    <div class="container topbar__container">
      <router-link :to="{ name: 'globalFeed' }" class="topbar__brand">
        Medium Clone
      </router-link>
      <ul class="topbar__list">
        <li class="topbar__item">
          <router-link
            :to="{ name: 'globalFeed' }"
            class="topbar__link"
            exact
            active-class="active-router-link"
          >
            Home
          </router-link>
          <div class="topbar__link-line"></div>
        </li>

        <template v-if="isLogin">
          <li class="topbar__item">
            <router-link
              :to="{ name: 'createArticle' }"
              class="topbar__link"
              active-class="active-router-link"
            >
              New Article
            </router-link>
            <div class="topbar__link-line"></div>
          </li>

          <li class="topbar__item">
            <router-link
              :to="{ name: 'settings' }"
              class="topbar__link"
              active-class="active-router-link"
            >
              Settings
            </router-link>
            <div class="topbar__link-line"></div>
          </li>

          <li class="topbar__item">
            <router-link
              :to="{
                name: 'userProfile',
                params: { slug: authUser.username },
              }"
              class="topbar__link"
              active-class="active-router-link"
            >
              <img
                class="topbar__user-img"
                :src="authUser.image"
                alt="user-image"
              />
              <p class="topbar__user-name">{{ authUser.username }}</p>
            </router-link>
            <div class="topbar__link-line"></div>
          </li>
        </template>

        <template v-if="isAnonymous">
          <li class="topbar__item">
            <router-link
              :to="{ name: 'login' }"
              class="topbar__link"
              active-class="active-router-link"
            >
              Sign in
            </router-link>
            <div class="topbar__link-line"></div>
          </li>
          <li class="topbar__item">
            <router-link
              :to="{ name: 'register' }"
              class="topbar__link"
              active-class="active-router-link"
            >
              Sign up
            </router-link>
            <div class="topbar__link-line"></div>
          </li>
        </template>
      </ul>
    </div>
  </nav>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "Topbar",
  computed: {
    ...mapGetters("auth", ["authUser", "isLogin", "isAnonymous"]),
  },
};
</script>

