<template>
  <div class="article-item">
    <Loading v-if="isLoading" />
    <Errors v-if="error" />

    <div v-if="articleInfo">
      <div v-if="articleInfo" class="article-page">
        <div class="article-item-banner">
          <div class="container">
            <h3 class="article-item__title">
              {{ articleInfo.title }}
            </h3>
            <div class="article-user">
              <router-link
                class="article-item__img"
                :to="{
                  name: 'userProfile',
                  params: { slug: articleInfo.author.username },
                }"
              >
                <img
                  :src="articleInfo.author.image"
                  alt="user-image"
                  class="topbar__user-img user-image"
                />
              </router-link>
              <div class="user-meta">
                <router-link
                  :to="{
                    name: 'userProfile',
                    params: { slug: articleInfo.author.username },
                  }"
                >
                  <p>
                    {{ articleInfo.author.username }}
                  </p>
                </router-link>
                <span class="article__update">
                  {{ articleInfo.updatedAt }}
                </span>
              </div>
              <AddToFavorite
                v-if="isLogin"
                :is-favorite="articleInfo.favorited"
                :favorites-count="articleInfo.favoritesCount"
                :articleSlug="articleInfo.slug"
              />
              <div class="article__btn-author" v-if="currentUserIsAuthor">
                <router-link
                  class="article__btn article__btn-edit"
                  :to="{
                    name: 'editArticle',
                    params: {
                      slug: articleInfo.slug,
                    },
                  }"
                >
                  Edit article
                </router-link>
                <button
                  class="article__btn article__btn-delete"
                  @click="deletethisArticle"
                >
                  Delete article
                </button>
              </div>
            </div>
            <TagList
              v-if="articleInfo.tagList"
              :tag-list="articleInfo.tagList"
            />
          </div>
        </div>
        <div class="container">
          <span class="article__desc-title">Description</span>
          <p class="article__desc">
            {{ articleInfo.description }}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import Loading from "./Loading.vue";
import Errors from "./Errors.vue";
import TagList from "@/components/TagList";
import AddToFavorite from "@/components/AddToFavorite";

export default {
  name: "Article",
  components: {
    Loading,
    Errors,
    TagList,
    AddToFavorite,
  },
  mounted() {
    this.getArticle(this.$route.params.slug);
    console.log(this.articleInfo);
  },
  methods: {
    ...mapActions("article", ["getArticle", "deleteArticle"]),
    deletethisArticle() {
      this.deleteArticle(this.$route.params.slug).then(
        this.$router.push({ name: "globalFeed" })
      );
    },
  },
  computed: {
    ...mapGetters("article", ["articleInfo", "isLoading", "error"]),
    ...mapGetters("auth", ["authUser", "isLogin"]),
    currentUserIsAuthor() {
      if (this.articleInfo && this.authUser) {
        return this.authUser.username === this.articleInfo.author.username;
      } else {
        return false;
      }
    },
    // isAuthorArticle() {
    //   return this.authUser.username === this.article.author.username;
    // },
  },
};
</script>

