import mutations from "../mutations"
import feed from "../../api/feed";

const {
    GET_FEED_START,
    GET_FEED_SUCCESS,
    GET_FEED_FAILED
} = mutations;

export default {
    namespaced: true,
    state: {
        feeds: null,
        isLoading: false,
        errors: null
    },
    getters: {
        feeds: ({ feeds }) => feeds,
        isLoading: ({ isLoading }) => isLoading,
        errors: ({ errors }) => errors,
    },
    mutations: {
        [GET_FEED_START](state) {
            state.isLoading = true;
            state.errors = null;
            state.feeds = null;
        },
        [GET_FEED_SUCCESS](state, data) {
            state.isLoading = false;
            state.feeds = data;
        },
        [GET_FEED_FAILED](state, errors) {
            state.isLoading = false;
            state.errors = errors;
            state.feeds = null;
        },
    },
    actions: {
        fetchFeeds({ commit }, { apiUrl }) {
            return new Promise(resolve => {
                commit(GET_FEED_START);
                feed.getFeed(apiUrl)
                    .then(response => {
                        commit(GET_FEED_SUCCESS, response.data);
                        return resolve(response.data);
                    })
                    .catch(errors => {
                        commit(GET_FEED_FAILED, errors);
                    })
            });
        }
    },
}