<template>
  <div>
    <Loading v-if="isLoading" />
    <ArticleForm
      @articleSubmit="onSubmit"
      :initial-values="initialValues"
      :errors="createError"
      :is-submitting="isSubmitting"
    />
  </div>
</template>

<script>
import ArticleForm from "./ArticleForm.vue";
import Loading from "./Loading.vue";

import { mapActions, mapGetters } from "vuex";

export default {
  name: "EditArticle",
  components: {
    ArticleForm,
    Loading,
  },
  mounted() {
    this.getArticle(this.$route.params.slug);
  },
  methods: {
    onSubmit(articleInput) {
      console.log(articleInput);
      const slug = this.$route.params.slug;
      this.updateArticle({ articleInput, slug }).then((article) => {
        this.$router.push({ name: "article", params: { slug: article.slug } });
      });
    },
    ...mapActions("article", ["getArticle", "updateArticle"]),
  },
  computed: {
    ...mapGetters("article", [
      "isSubmitting",
      "createError",
      "isLoading",
      "articleInfo",
    ]),
    initialValues() {
      if (!this.articleInfo) {
        return {
          title: "",
          description: "",
          body: "",
          tags: "",
        };
      }
      return {
        title: this.articleInfo.title,
        description: this.articleInfo.description,
        body: this.articleInfo.body,
        tags: this.articleInfo.tagList,
      };
    },
  },
};
</script>

