import userProfileApi from "@/api/userProfile";
import mutations from "../mutations";

const {
    GET_USER_PROFILE_START,
    GET_USER_PROFILE_SUCCESS,
    GET_USER_PROFILE_FAILED
} = mutations;

export default {
    namespaced: true,
    state: {
        isLoading: false,
        errors: null,
        userProfile: null,
    },
    getters: {
        isLoading: (state) => state.isLoading,
        errors: (state) => state.errors,
        userProfileData: (state) => state.userProfile,
    },
    mutations: {
        [GET_USER_PROFILE_START](state) {
            state.isLoading = true;
            state.errors = null;
        },
        [GET_USER_PROFILE_SUCCESS](state, data) {
            state.isLoading = false;
            state.userProfile = data;
        },
        [GET_USER_PROFILE_FAILED](state) {
            state.isLoading = false;
        },
    },
    actions: {
        getUserProfile({ commit }, { slug }) {
            return new Promise(resolve => {
                commit(GET_USER_PROFILE_START);
                userProfileApi
                    .getUserProfile(slug)
                    .then((userProfile) => {
                        console.log(userProfile);
                        commit(GET_USER_PROFILE_SUCCESS, userProfile);
                        resolve(userProfile);
                    })
                    .catch(() => {
                        commit(GET_USER_PROFILE_FAILED);
                    });
            })
        }
    },
}