<template>
  <div class="container settings__container">
    <div class="settings__title">Your Settings</div>
    <form
      class="settings__form"
      v-if="authUser"
      @submit.prevent="submitSettings"
    >
      <label class="settings__label">
        <input
          class="settings__input article__input"
          type="text"
          placeholder="URL of profile picture"
          v-model="form.image"
        />
      </label>
      <label class="settings__label">
        <input
          class="settings__input article__input"
          type="text"
          placeholder="Username"
          v-model="form.username"
        />
      </label>
      <label class="settings__label">
        <textarea
          class="settings__textarea article__textarea"
          placeholder="Short bio about you"
          v-model="form.bio"
        ></textarea>
      </label>
      <label class="settings__label">
        <input
          class="settings__input article__input"
          type="email"
          placeholder="Email"
          v-model="form.email"
        />
      </label>
      <label class="settings__label">
        <input
          class="settings__input article__input"
          type="password"
          placeholder="New Password"
          v-model="form.password"
        />
      </label>
      <button
        type="submit"
        class="settings__btn settings__btn-success article__form-btn"
      >
        Update Settings
      </button>
    </form>
    <button
      class="settings__btn settings__btn-danger article__form-btn"
      @click="onLogout"
    >
      Or click here to logout
    </button>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Settings",
  computed: {
    ...mapGetters("auth", ["authUser"]),
    form() {
      return {
        username: this.authUser.username,
        image: this.authUser.image,
        bio: this.authUser.bio,
        email: this.authUser.email,
        password: "",
      };
    },
  },
  methods: {
    ...mapActions("auth", ["updateCurrentUser", "logout"]),
    submitSettings() {
      console.log(this.form);
      this.updateCurrentUser({ settingInput: this.form }).then(() => {
        this.$router.push({ name: "globalFeed" });
      });
    },
    onLogout() {
      console.log("logout");
      this.logout().then(() => {
        this.$router.push({ name: "globalFeed" });
      });
    },
  },
};
</script>

