import axios from '@/api/axios';

const addToFavotite = (slug) => axios.post(`/articles/${slug}/favorite`).then(response => response.data.article)
const deleteFromFavotite = (slug) => axios.delete(`/articles/${slug}/favorite`).then(response => response.data.article)

export default {
    addToFavotite,
    deleteFromFavotite
}