<template>
  <main class="main">
    <Banner />
    <ToggleFeeds :tag-name="tagName" />
    <div class="container global-feed__container">
      <Feed :apiUrl="apiUrl" />
      <div class="tags-components">
        <PopularTags />
      </div>
    </div>
  </main>
</template>

<script>
import Feed from "@/components/Feed";
import PopularTags from "@/components/PopularTags";
import Banner from "@/components/Banner";
import ToggleFeeds from "@/components/ToggleFeeds";

export default {
  name: "TagsFeed",
  components: {
    Feed,
    PopularTags,
    Banner,
    ToggleFeeds,
  },
  computed: {
    tagName() {
      return this.$route.params.slug;
    },
    apiUrl() {
      return`/articles?tag=${this.$route.params.slug}`;
    },
  },
};
</script>

