<template>
  <div class="popular-tags">
    <!-- <Loading v-if="isLoading" /> -->
    <Errors v-if="errors" :message="'tags component has something errors'" />
    <ul v-if="popularTags" class="popular-tags__list">
      <li
        class="popular-tags__item"
        v-for="popularTag in popularTags"
        :key="popularTag"
      >
        <router-link
          v-if="popularTag"
          :to="{ name: 'tag', params: { slug: popularTag } }"
        >
          # {{ popularTag }}
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
// import Loading from "./Loading.vue";
import Errors from "./Errors.vue";
import { mapActions, mapGetters } from "vuex";

export default {
  name: "PopularTags",
  components: {
    // Loading,
    Errors,
  },
  mounted() {
    this.fetchPopularTags();
  },
  computed: {
    ...mapGetters("popularTags", ["popularTags", "isLoading", "errors"]),
  },
  methods: {
    ...mapActions("popularTags", ["fetchPopularTags"]),
  },
};
</script>

