<template>
  <div class="container toggle-feed__container">
    <ul class="toggle-feed__list">
      <li class="toggle-feed__item" v-if="isLogin">
        <router-link
          class="toggle-feed__link"
          :to="{ name: 'yuorFeed' }"
          :class="{ 'active-feed': currentPathYourFeed }"
          >Your Feed</router-link
        >
      </li>

      <li class="toggle-feed__item">
        <router-link
          class="toggle-feed__link"
          :to="{ name: 'globalFeed' }"
          :class="{ 'active-feed': currentPathHome }"
          >Global Feed</router-link
        >
      </li>

      <li class="toggle-feed__item" v-if="tagName">
        <router-link
          class="toggle-feed__link"
          :to="{ name: 'tag', params: { slug: tagName } }"
          :class="{ 'active-feed': $route.path == `/tags/${tagName}` }"
          ># {{ tagName }}</router-link
        >
      </li>
    </ul>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "ToggleFeeds",
  props: {
    tagName: {
      type: String,
      required: false,
      default: "",
    },
  },
  mounted() {
    console.log(this.$route.path);
  },
  computed: {
    ...mapGetters("auth", ["isLogin"]),
    currentPathHome() {
      return this.$route.path === "/";
    },
    currentPathYourFeed() {
      return this.$route.path === "/feed";
    },
  },
};
</script>

